import styled from 'styled-components'

export default function SmallBtn({ text, onClick, type, hoverColor }) {
  return (
    <BtnEl type={type} onClick={onClick} hoverColor={hoverColor}>
      {text}
    </BtnEl>
  )
}

const BtnEl = styled.button`
  display: block;
  border: 3px solid #e6e6e6;
  border-radius: 10px;
  background-color: white;
  cursor: pointer;
  padding: 5px 10px;
  color: #6e6e6e;
  font-size: 15px;
  box-sizing: border-box;
  z-index: 2;
  font-family: 'Noto Sans KR', sans-serif;

  :hover {
    border: ${(props) => {
      if (props.hoverColor) {
        return `3px solid ${props.hoverColor}`
      } else {
        return `3px solid black`
      }
    }};
    color: ${(props) => {
      if (props.hoverColor) {
        return `${props.hoverColor}`
      } else {
        return `black`
      }
    }};
    font-weight: 700;
  }
`
