import SelectBox from '../../elements/SelectBox'
import InputBox from '../../elements/InputBox'
import PhoneInput from '../../elements/PhoneInput'
import Form from '../../elements/Form'
import { useState } from 'react'
import { Route, Routes } from 'react-router-dom'
import UserInfoAdmin from './UserInfoAdmin'
import { searchUserData } from '../../firebase/firebase'
import UserInfoForm from './UserInfoForm'

export default function UserAdmin() {
  const [phone, setPhone] = useState('')
  const [info, setInfo] = useState({ name: '' })
  const [disable, setDisable] = useState(false)
  const [show, setShow] = useState(false)
  const handlePhoneNumberSubmit = (e) => {
    if (!disable) {
      if (phone.length >= 10) {
        searchUserData(phone, setInfo, setShow)
      } else {
        alert('번호를 입력해주세요.')
      }
    }
  }
  return (
    <SelectBox>
      <Form onSubmit={handlePhoneNumberSubmit} btnText='검색'>
        <InputBox label='휴대폰 번호' name='phoneNumber'>
          <PhoneInput name='phoneNumber' setState={setPhone} disabled={disable} />
        </InputBox>
      </Form>
      <Routes>
        <Route
          path='/'
          element={<UserInfoAdmin info={info} setDisable={setDisable} show={show} setShow={setShow} />}
        ></Route>
        <Route
          path={`/alterclass`}
          element={<UserInfoForm info={info} setInfo={setInfo} setDisable={setDisable} />}
        ></Route>
      </Routes>
    </SelectBox>
  )
}
