import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import styled from 'styled-components'

import H2 from '../../elements/H2'
import SelectBox from '../../elements/SelectBox'
import Ul from '../../elements/Ul'
import { promiseCollectionsData } from '../../firebase/firebase'

export default function List({ userInfo }) {
  const nav = useNavigate()
  const [dataArr, setDataArr] = useState([])

  useEffect(() => {
    const preClass = sessionStorage.getItem('pre_class')
    if (userInfo.isClass) {
      nav('/main')
    }
    //해당 교육 collection에서 등록된 교육일 리스트 가졍오기
    promiseCollectionsData(preClass)
      .then((res) => {
        let dataArr = []
        res.forEach((doc) => {
          if (doc.data().timeArr.length > 0) {
            if (doc.data().date[0] !== '1') {
              dataArr.push(doc.data().date)
            }
          }
        })
        setDataArr(dataArr)
      })
      .catch((error) => {
        console.error(error)
      })
  }, [nav, userInfo.isClass])

  return (
    <SelectBox>
      <ClassName>{sessionStorage.getItem('pre_class')}</ClassName>
      <H2 text='교육일을 선택해 주세요.' fontsize='1.1rem' margin='30px 0 0 0' />
      <Ul
        arr={dataArr}
        onClick={(item) => {
          nav('/main/time')
          sessionStorage.setItem('pre_date', item)
        }}
        list='date'
      />
    </SelectBox>
  )
}
const ClassName = styled.div`
  font-size: 1.1rem;
  margin-top: 0.5rem;
  margin-bottom: -0.5rem;
`
