import { useState } from 'react'
import { Route, Routes } from 'react-router-dom'
import Index from './pages/Index'
import Main from './pages/Main'
// import Signin from "./pages/Signin";
import Certification from './pages/Iamport'
import SigninSuccess from './pages/SigninSuccess'
import styled from 'styled-components'

export default function App() {
  const [userInfo, setUserInfo] = useState({
    name: '',
    phoneNumber: '',
    birth: '',
    belong: '',
    class: '',
    isClass: false,
  })

  return (
    <AppPage className='App'>
      <Routes>
        <Route path='/' element={<Index userInfo={userInfo} setUserInfo={setUserInfo} />}></Route>
        <Route path='/signin' element={<Certification />}></Route>
        <Route path='/signin-success' element={<SigninSuccess />}></Route>
        <Route path='/main/*' element={<Main userInfo={userInfo} setUserInfo={setUserInfo} />}></Route>
      </Routes>
    </AppPage>
  )
}

const AppPage = styled.div`
  width: 100vw;
  height: 100vh;
  overflow: hidden;
`
