import styled from 'styled-components'
import { dayArr } from '../store/Data'

export default function Ul({ arr, onClick, list }) {
  return (
    <UlEl>
      {list === 'time'
        ? arr.timeArr.sort().map((item, index) => {
            let className = 'able'
            if (item.entry.length >= item.personnal) {
              className = 'disabled'
            } else {
              className = 'abled'
            }
            return (
              <LiEl onClick={() => onClick(item)} key={`${item.time}${index}`} className={className}>
                {`${item.time.slice(0, 2)}시 ${item.time.slice(2, 4)}분`} ({`${item.fewMinute}분 교육`}){' '}
                {item.entry.length >= item.personnal ? '마감' : `${item.entry.length} 명 신청`}
              </LiEl>
            )
          })
        : arr.sort().map((item, index) => {
            const today = new Date()
            const stringItem = String(item)
            const itemYear = Number(stringItem.slice(0, 4))
            const itemMonth = Number(stringItem.slice(4, 6))
            const itemDate = Number(stringItem.slice(6, 8))
            const itemday = new Date(`${itemYear}-${itemMonth}-${itemDate}`)
            const oneDay = 60 * 60 * 24

            if (itemday.getTime() - today.getTime < oneDay * 4) {
              return (
                <LiEl key={`${stringItem}${index}`} className='disabled'>
                  {stringItem.slice(0, 4)}년 {stringItem.slice(4, 6)}월 {stringItem.slice(6, 8)}일{' '}
                  {dayArr[stringItem.slice(8, 9)]}
                  요일 (마감)
                </LiEl>
              )
            } else {
              return (
                <LiEl onClick={() => onClick(item)} key={`${item}${index}`}>
                  {stringItem.slice(0, 4)}년 {stringItem.slice(4, 6)}월 {stringItem.slice(6, 8)}일{' '}
                  {dayArr[stringItem.slice(8, 9)]}
                  요일
                </LiEl>
              )
            }
          })}
    </UlEl>
  )
}

const UlEl = styled.ul`
  display: flex;
  width: 320px;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  height: 67%;
  overflow: scroll;
  background-color: white;
  border-radius: 5px;
  padding: 5px;
  transition: all 0.2s ease-out;
  font-family: 'Noto Sans KR', sans-serif;
`

const LiEl = styled.li`
  width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  margin: 5px 0;
  border: 3px solid #e6e6e6;
  border-radius: 5px;
  cursor: pointer;
  padding: 12px 0;
  z-index: 2;
  font-family: 'Noto Sans KR', sans-serif;

  :hover {
    border: 3px solid black;
    font-weight: 600;
    color: blue;
    &.disabled {
      color: #6e6e6e;
      border: 3px solid #e6e6e6;
      font-weight: 400;
    }
  }
  &.disabled {
    color: #6e6e6e;
  }
`
