import styled from 'styled-components'

export default function InputBox2({ name, setState, fontSize, width, selectArr, onChange, text }) {
  return (
    <InputBox>
      <Label>{text}</Label>
      <SelectEl name={name} id={name} onChange={(e) => onChange(e, setState)} width={width} fontSize={fontSize}>
        {selectArr.map((item, index) => {
          return (
            <option value={item} key={`${item}${index}`}>
              {item}
            </option>
          )
        })}
      </SelectEl>
    </InputBox>
  )
}
const InputBox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  z-index: 2;
  font-family: 'Noto Sans KR', sans-serif;
`
const Label = styled.div`
  text-align: center;
  width: 100px;
  font-size: 17px;
`
const SelectEl = styled.select`
  text-align: center;
  border: none;
  border-radius: 0;
  background-color: transparent;
  border-bottom: 2px solid #6e6e6e;
  font-size: ${(props) => {
    if (props.fontSize) {
      return props.fontSize
    } else {
      return '20px'
    }
  }};
  margin: 5px 0;
  width: ${(props) => {
    return props.width
  }};
  padding: 10px 0;
  overflow: auto;
`
