import { useEffect, useState } from 'react'
import { Outlet, Route, Routes, useLocation, useNavigate } from 'react-router-dom'
import BackBtn from '../elements/BackBtn'
import Btn from '../elements/Btn'
import Header from '../elements/Header'
import MyClass from '../elements/MyClass'
import Page from '../elements/Page'
import SelectBox from '../elements/SelectBox'
import SmallBtn from '../elements/SmallBtn'
import UserInfo from '../elements/UserInfo'
import { confirmClassData, readScheduleData, setClassData, setUserData } from '../firebase/firebase'
import ClassList from './main/ClassList'
import List from './main/List'
import Master from './main/Master'
import Result from './main/Result'
import TimeList from './main/TimeList'
import AddClass from './main/AddClass'
import { todayDate } from '../store/Data'
import ClassData from './main/ClassData'
import ClassAdmin from './main/ClassAdmin'
import UserAdmin from './classAdmin/UserAdmin'
import DeleteClass from './main/DeleteClass'
import DeleteClassDate from './main/DeleteClassDate'
import Footer from '../elements/Footer'

export default function Main({ setUserInfo, userInfo }) {
  const nav = useNavigate()
  const location = useLocation()
  const [goBack, setGoBack] = useState(true)
  const [showBtn, setShowBtn] = useState(true)
  const [timeData, setTimeData] = useState({
    timeArr: [
      {
        time: '',
        entry: [{ name: '', phoneNumber: '', birth: '', belong: '' }],
        personnal: 0,
      },
    ],
  })
  useEffect(() => {
    if (!userInfo.phoneNumber) {
      nav('/')
    } else if (!userInfo.isMaster) {
      if (userInfo.phoneNumber === '01077775228') {
        nav('/main/controller')
      } else if (userInfo.phoneNumber === '01099999999') {
        nav('/main/controller')
      }
    }
    if (location.pathname === '/main/controller') {
      setGoBack(false)
    } else {
      setGoBack(true)
    }
    const classArr = userInfo.class.split('/')
    if (userInfo.isClass) {
      readScheduleData(classArr[0], classArr[1], setTimeData)
      confirmClassData(classArr[0], classArr[1], classArr[2], setUserInfo, userInfo)
    }
  }, [location.pathname, nav, setUserInfo, userInfo, userInfo.class, userInfo.isClass, userInfo.phoneNumber])
  const logout = () => {
    sessionStorage.clear()
    setUserInfo({
      name: '',
      phoneNumber: '',
      birth: '',
      belong: '',
      class: '',
      id: '',
      isClass: false,
    })
    setTimeData({
      timeArr: [
        {
          time: '',
          entry: [{ name: '', phoneNumber: '', birth: '', belong: '' }],
          personnal: 0,
        },
      ],
    })
    nav('/')
  }

  const handleCancelClick = () => {
    const classArr = userInfo.class.split('/')
    if (
      Number(todayDate.substring(0, todayDate.length - 1)) <=
      Number(classArr[1].substring(0, classArr[1].length - 1)) - 2
    ) {
      const userConfirm = window.confirm('정말 수강을 취소하시겠습니까?')
      if (userConfirm) {
        const timearr = timeData.timeArr.map((item) => {
          if (item.time === classArr[2]) {
            const entryArr = item.entry.filter((i) => i.phoneNumber !== userInfo.phoneNumber)
            item.entry = entryArr
            return item
          } else {
            return item
          }
        })
        const timeobj = { date: classArr[1], timeArr: timearr }
        setClassData(classArr[0], classArr[1], timeobj)
        setUserInfo({ ...userInfo, class: '', isClass: false })
        const obj = { ...userInfo, class: '', isClass: false }
        setUserData('user', obj)
      }
    } else {
      alert('교육 취소는 신청한 교육일 2일 전까지만 가능합니다.')
    }
  }
  return (
    <Page>
      <Header>
        {goBack && <BackBtn top='0px' left='0px' />}
        <SmallBtn text='로그아웃' onClick={logout} hoverColor='red' />
      </Header>
      <UserInfo name={userInfo.name} belong={userInfo.belong} />
      {userInfo.isClass ? (
        <SelectBox>
          <MyClass myclass={userInfo.class} text='교육 신청 완료!' fontSize='30px' />
          {showBtn ? (
            <Btn onClick={handleCancelClick} text='교육 취소' />
          ) : (
            <div>* 수강취소는 다시 로그인 후 사용하실 수 있습니다.</div>
          )}
        </SelectBox>
      ) : (
        <Routes>
          <Route path='/' element={<ClassList userInfo={userInfo} />}></Route>
          <Route path='/date' element={<List userInfo={userInfo} />}></Route>
          <Route
            path='/time'
            element={
              <TimeList userInfo={userInfo} setUserInfo={setUserInfo} timeData={timeData} setTimeData={setTimeData} />
            }
          ></Route>
          <Route
            path='/result'
            element={
              <Result
                userInfo={userInfo}
                setUserInfo={setUserInfo}
                timeData={timeData}
                setTimeData={setTimeData}
                setShowBtn={setShowBtn}
              />
            }
          ></Route>
          <Route path='/controller' element={<Master userInfo={userInfo} setUserInfo={setUserInfo} />}></Route>
          <Route path='/addclass' element={<AddClass />}></Route>
          <Route path='/classadmin/*' element={<ClassAdmin />}></Route>
          <Route path='/data/*' element={<ClassData />}></Route>
          <Route path='/useradmin/*' element={<UserAdmin />}></Route>
          <Route path='/deleteclass' element={<DeleteClass />}></Route>
          <Route path='/deleteclassdate' element={<DeleteClassDate />}></Route>
        </Routes>
      )}
      <Outlet />
      <Footer />
    </Page>
  )
}
