import { useState } from 'react'
import { Route, Routes } from 'react-router-dom'
import ClassChoice from '../classAdmin/ClassChoice'
import ClassState from '../classAdmin/ClassState'

export default function ClassAdmin() {
  const [data, setData] = useState('user')
  return (
    <Routes>
      <Route path='/' element={<ClassChoice data={data} setData={setData} />}></Route>
      <Route path='/state' element={<ClassState data={data} setData={setData} />}></Route>
    </Routes>
  )
}
