import { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import styled from 'styled-components'
import H2 from '../../elements/H2'
import SelectBox from '../../elements/SelectBox'
import Ul from '../../elements/Ul'
import { readScheduleData } from '../../firebase/firebase'
import { DAY } from '../../utils/constant'

export default function TimeList({ timeData, setTimeData, userInfo }) {
  const nav = useNavigate()
  useEffect(() => {
    if (userInfo.isClass) {
      nav('/main')
    }
    readScheduleData(sessionStorage.getItem('pre_class'), sessionStorage.getItem('pre_date'), setTimeData)
  }, [nav, setTimeData, userInfo.isClass])
  const pre_date = sessionStorage.getItem('pre_date')
  const pre_day = DAY[pre_date.slice(8, 9)]
  return (
    <SelectBox>
      <ClassName>{sessionStorage.getItem('pre_class')}</ClassName>
      <ClassDate>{`${pre_date.slice(0, 4)}년 ${pre_date.slice(4, 6)}월 ${pre_date.slice(
        6,
        8
      )}일 ${pre_day}요일`}</ClassDate>
      <H2 text='교육시간을 선택해 주세요.' fontsize='1.1rem' margin='30px 0 0 0' />
      <Ul
        arr={timeData}
        onClick={(item) => {
          if (item.entry.length >= item.personnal) {
            alert('마감되었습니다. 다른 시간을 선택해 주세요.')
          } else {
            nav('/main/result')
            sessionStorage.setItem('pre_time', item.time)
            sessionStorage.setItem('pre_fewMinute', item.fewMinute)
          }
        }}
        list='time'
      />
    </SelectBox>
  )
}

const ClassName = styled.div`
  font-size: 1.1rem;
  margin-top: 0.5rem;
`
const ClassDate = styled.div`
  font-size: 1.1rem;
  margin-top: 0.5rem;
  margin-bottom: -1rem;
`
