import { useEffect, useState } from 'react'
import styled from 'styled-components'
import ListClassData from '../../elements/ListClassData'
import SelectBox from '../../elements/SelectBox'
import SmallBtn from '../../elements/SmallBtn'
import { downloadClassExcel, readCollectionData } from '../../firebase/firebase'
import { classIdStamp, getBarPlus6Day } from '../../utils/dateStamp'

export default function ClassState({ data }) {
  const today = getToday()
  const daylater = getBarPlus6Day(today)
  const [classData, setClassData] = useState([])
  const [startDate, setStartDate] = useState(today)
  const [endDate, setEndDate] = useState(daylater)
  function getToday() {
    var date = new Date()
    var year = date.getFullYear()
    var month = ('0' + (1 + date.getMonth())).slice(-2)
    var day = ('0' + date.getDate()).slice(-2)

    return year + '-' + month + '-' + day
  }
  useEffect(() => {
    const startClassDate = classIdStamp(startDate)
    const endClassDate = classIdStamp(endDate)
    readCollectionData(data, setClassData, startClassDate, endClassDate)
  }, [data, endDate, startDate])

  // const handleClick = (e) => {
  //   const date = e.currentTarget.dataset.date
  //   const time = e.currentTarget.dataset.time
  //   const personnal = e.currentTarget.dataset.personnal
  //   const fewMinute = e.currentTarget.dataset.fewminute
  //   const index = e.currentTarget.dataset.index
  //   const classObj = {
  //     class: data,
  //     date: date,
  //     time: time,
  //     personnal: personnal,
  //     fewMinute: fewMinute,
  //     index: index,
  //   }
  // }

  const handleStartDateChange = (e) => {
    const startDate = e.currentTarget.value
    setStartDate(startDate)
  }
  const handleEndDateChange = (e) => {
    const endDate = e.currentTarget.value
    setEndDate(endDate)
  }
  const downloadHandler = () => {
    downloadClassExcel(`${data}표_${startDate}-${endDate}`, classData, data)
  }
  return (
    <SelectBox>
      <ClassNameBox>교육명 : {data}</ClassNameBox>
      <OptionBox>
        <OptionDate>
          <input type='date' value={startDate} onChange={handleStartDateChange} /> <div>~</div>{' '}
          <input type='date' value={endDate} onChange={handleEndDateChange} />
        </OptionDate>
        <SmallBtn text='다운로드' onClick={downloadHandler} type='button' />
      </OptionBox>
      <ListClassData classData={classData} data={data} />
    </SelectBox>
  )
}

const OptionBox = styled.div`
  width: 70%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px;
  @media screen and (max-width: 750px) {
    width: 60%;
  }
  @media screen and (max-width: 630px) {
    flex-direction: column;
    div {
      margin-bottom: 10px;
    }
    button {
      margin-bottom: 10px;
    }
  }
`

const OptionDate = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  div {
    margin: 0 5px;
  }
`
const ClassNameBox = styled.div`
  font-size: 0.8rem;
  margin-bottom: 5px;
`
