import { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import Btn from '../../elements/Btn'
import SelectBox from '../../elements/SelectBox'
// import { addDocData } from '../../firebase/firebase'
// import { USERCLASSDATA } from '../../store/UserClassData'

export default function Master({ userInfo, setUserInfo }) {
  // const [count, setCount] = useState(0)
  const nav = useNavigate()

  useEffect(() => {
    if (userInfo.phoneNumber !== '01099999999' && userInfo.phoneNumber !== '01077775228') {
      nav('/')
    }
    setUserInfo((data) => ({ ...data, isMaster: true }))
  }, [nav, setUserInfo, userInfo.phoneNumber])

  // const addJsonData = () => {
  //   console.log(count)
  //   addDocData(USERCLASSDATA[count])
  //   setCount((prev) => prev + 1)
  // }
  return (
    <SelectBox>
      <Btn
        onClick={() => {
          nav('/main/data')
        }}
        text='교육 신청 현황'
      />
      <Btn
        onClick={() => {
          nav('/main/addclass')
        }}
        text='교육 프로그램 생성, 수정, 삭제'
      />
      <Btn
        onClick={() => {
          nav('/main/classadmin')
        }}
        text='교육 프로그램 현황'
      />
      <Btn
        onClick={() => {
          nav('/main/useradmin')
        }}
        text='회원 교육 정보 확인'
      />
      <Btn
        onClick={() => {
          nav('/main/deleteclass')
        }}
        text='교육 삭제'
      />
      {/* <Btn onClick={addJsonData} text='데이터 추가' /> */}
    </SelectBox>
  )
}
