import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import BigBtn from '../../elements/BigBtn'
import SelectBox from '../../elements/SelectBox'
import { classListData } from '../../firebase/firebase'

export default function ChoiceData({ setData }) {
  const nav = useNavigate()
  const [classList, setClassList] = useState([])
  const clickHandler = (e) => {
    const classname = e.currentTarget.name
    setData(classname)
    nav('/main/data/datalist')
  }
  useEffect(() => {
    classListData(setClassList)
  }, [])
  return (
    <SelectBox>
      {classList.map((item, index) => {
        return <BigBtn onClick={clickHandler} text={item} key={`${item}${index}class`} />
      })}
    </SelectBox>
  )
}
