export const dayArr = ['일', '월', '화', '수', '목', '금', '토']

const today = new Date()
let year = today.getFullYear()
let month = today.getMonth() + 1
let date = today.getDate()
export const TODAYNUM = year + DAYNUMARR[month] + date

if (month < 10) {
  month = `0${month}`
}
if (date < 10) {
  date = `0${date}`
}
export const todayDate = `${today.getFullYear()}${month}${date}${today.getDay()}`
