import styled from 'styled-components'
import { useLocation, useNavigate } from 'react-router-dom'
import { BackIcon } from '../assets/svgs'

export default function BackBtn({ top, left }) {
  const nav = useNavigate()
  const location = useLocation()

  const backMove = () => {
    nav(-1)
  }
  return (
    <BtnEl onClick={backMove} top={top} left={left}>
      {location.pathname !== '/main' && (
        <div className='wrap'>
          <BackIcon className='backIcon' />
          <div className='text'>뒤로가기</div>
        </div>
      )}
    </BtnEl>
  )
}

const BtnEl = styled.button`
  cursor: pointer;
  background-color: transparent;
  border: none;
  position: absolute;
  top: ${(props) => props.top};
  left: ${(props) => props.left};
  display: flex;
  justify-self: center;
  align-items: center;
  z-index: 2;
  font-family: 'Noto Sans KR', sans-serif;

  .wrap {
    display: flex;
    justify-self: center;
    align-items: center;
    .backIcon {
      height: 43px;
      width: 43px;
    }
    .text {
      color: black;
      font-weight: 700;
    }
  }
`
