import { useEffect, useState } from 'react'
import styled from 'styled-components'
import DeleteUl from '../../elements/DeleteUl'
import H2 from '../../elements/H2'
import SelectBox from '../../elements/SelectBox'
import { deleteDocData, getDocsData, promiseCollectionsData, promiseDeleteClass } from '../../firebase/firebase'

export default function List() {
  const [dataArr, setDataArr] = useState([])
  useEffect(() => {
    const deleteClass = sessionStorage.getItem('delete_class')
    promiseCollectionsData(deleteClass)
      .then((res) => {
        let dataArr = []
        res.forEach((doc) => {
          if (doc.data().timeArr.length > 0) {
            if (doc.data().date[0] !== '1') {
              dataArr.push(doc.data().date)
            }
          }
        })
        setDataArr(dataArr)
      })
      .catch((error) => {
        console.error(error)
      })
  }, [])

  const handleDeleteClick = (date) => {
    const isConfirm = window.confirm(
      '해당 교육일의 모든 데이터를 삭제 합니다. 데이터는 복구되지 않습니다. 정말 삭제하시겠습니까?'
    )
    if (isConfirm) {
      // deleteClass(sessionStorage.getItem('delete_class'), item, setDataArr)
      const classname = sessionStorage.getItem('delete_class')
      const deleteClassId = sessionStorage.getItem('delete_classId')

      getDocsData(classname)
        .then((res) => {
          let existDoc = 0
          res.forEach(() => {
            existDoc = existDoc + 1
          })
          console.log(existDoc)
          if (existDoc === 1) {
            promiseDeleteClass(classname, date)
              .then(() => {
                setDataArr((prev) => prev.filter((d) => d !== date))
                deleteDocData('classList', deleteClassId)
                alert('삭제 완료!')
              })
              .catch((error) => {
                console.error(error)
              })
          } else {
            promiseDeleteClass(classname, date)
              .then(() => {
                setDataArr((prev) => prev.filter((d) => d !== date))
                alert('삭제 완료!')
              })
              .catch((error) => {
                console.error(error)
              })
          }
        })
        .catch()
    }
  }
  return (
    <SelectBox>
      <ClassName>{sessionStorage.getItem('delete_class')}</ClassName>
      <H2 text='삭제할 교육일을 선택해 주세요.' fontsize='1.1rem' margin='30px 0 0 0' />
      <DeleteUl arr={dataArr} onClick={handleDeleteClick} list='date' />
    </SelectBox>
  )
}
const ClassName = styled.div`
  font-size: 1.1rem;
  margin-top: 0.5rem;
  margin-bottom: -0.5rem;
`
