import styled from 'styled-components'

export default function InputBox3({
  name,
  setState,
  fontSize,
  width,
  selectArr,
  onChange,
  text,
  name2,
  setState2,
  fontSize2,
  width2,
  selectArr2,
  onChange2,
  text2,
}) {
  return (
    <InputBox>
      <SelectEl name={name} id={name} onChange={(e) => onChange(e, setState)} width={width} fontSize={fontSize}>
        {selectArr.map((item, index) => {
          return (
            <option value={item} key={`${item}${index}`}>
              {item}
            </option>
          )
        })}
      </SelectEl>
      <Label>{text}</Label>

      <SelectEl name={name2} id={name2} onChange={(e) => onChange2(e, setState2)} width={width2} fontSize={fontSize2}>
        {selectArr2.map((item, index) => {
          return (
            <option value={item} key={`${item}${index}`}>
              {item}
            </option>
          )
        })}
      </SelectEl>
      <Label>{text2}</Label>
    </InputBox>
  )
}
const InputBox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  z-index: 2;
  font-family: 'Noto Sans KR', sans-serif;
`
const Label = styled.div`
  text-align: center;
  width: 50px;
  font-size: 20px;
`
const SelectEl = styled.select`
  text-align: center;
  border: none;
  border-radius: 0;
  background-color: transparent;
  border-bottom: 2px solid #6e6e6e;
  font-size: ${(props) => {
    if (props.fontSize) {
      return props.fontSize
    } else {
      return '20px'
    }
  }};
  margin: 10px 0;
  width: ${(props) => {
    return props.width
  }};
  padding: 10px 0;
`
