import { useEffect, useState } from 'react'
import styled from 'styled-components'
import ListData from '../../elements/ListData'
import SelectBox from '../../elements/SelectBox'
import SmallBtn from '../../elements/SmallBtn'
import { downloadExcel, readCollectionData } from '../../firebase/firebase'
import { classIdStamp, getBarPlus6Day, getBarToday } from '../../utils/dateStamp'

export default function DataList({ data }) {
  const today = getBarToday()
  const daylater = getBarPlus6Day(today)
  const [classData, setClassData] = useState([])
  const [startDate, setStartDate] = useState(today)
  const [endDate, setEndDate] = useState(daylater)
  useEffect(() => {
    const startClassDate = classIdStamp(startDate)
    const endClassDate = classIdStamp(endDate)
    readCollectionData(data, setClassData, startClassDate, endClassDate)
  }, [data, endDate, startDate])
  const downloadHandler = () => {
    downloadExcel(`${data}_${startDate}-${endDate}`, classData, data)
  }
  const handleStartDateChange = (e) => {
    const startDate = e.currentTarget.value
    setStartDate(startDate)
  }
  const handleEndDateChange = (e) => {
    const endDate = e.currentTarget.value
    setEndDate(endDate)
  }
  return (
    <SelectBox>
      <ClassNameBox>교육명 : {data}</ClassNameBox>
      <OptionBox>
        <OptionDate>
          <input type='date' value={startDate} onChange={handleStartDateChange} /> <div>~</div>
          <input type='date' value={endDate} onChange={handleEndDateChange} />
        </OptionDate>

        <SmallBtn text='다운로드' onClick={downloadHandler} type='button' />
      </OptionBox>
      <ListData classData={classData} />
    </SelectBox>
  )
}

const OptionBox = styled.div`
  width: 70%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px;
  @media screen and (max-width: 750px) {
    width: 60%;
  }
  @media screen and (max-width: 630px) {
    flex-direction: column;
    div {
      margin-bottom: 10px;
    }
    button {
      margin-bottom: 10px;
    }
  }
`

const OptionDate = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  div {
    margin: 0 5px;
  }
`
const ClassNameBox = styled.div`
  font-size: 0.8rem;
  margin-bottom: 5px;
`
