import { useNavigate } from 'react-router-dom'
import Btn from '../elements/Btn'
import Input from '../elements/Input'
import PhoneInput from '../elements/PhoneInput'
import { useState, useEffect } from 'react'
import { loginData, auth } from '../firebase/firebase'
import { signOut } from 'firebase/auth'
import InputBox from '../elements/InputBox'
import Page from '../elements/Page'
import Form from '../elements/Form'
import Header from '../elements/Header'
import PageLogo from '../elements/PageLogo'
import styled from 'styled-components'
import Footer from '../elements/Footer'

export default function Index({ userInfo, setUserInfo }) {
  const nav = useNavigate()
  const [phoneNumber, setPhoneNumber] = useState('')
  const [birth, setBirth] = useState('')
  useEffect(() => {
    sessionStorage.clear()
    signOut(auth)
      .then(() => {
        // Sign-out successful.
      })
      .catch((error) => {
        // An error happened.
      })
  }, [])
  const changeInput = (e, setState) => {
    const name = e.target.name
    let num = e.target.value
    if (name === 'phoneNumber') {
      num = `010${e.target.value}`
    } else {
      num = `${e.target.value}`
    }
    setState(num)
  }
  return (
    <Page>
      <Header />
      <PageLogo text='교육을 신청하고 신청 교육을 확인할 수 있습니다.' text2='회원 가입 후 이용해 주세요.' />
      <LoginBox>
        <Form
          action='submit'
          onSubmit={(e) => {
            e.preventDefault()
            const obj = { birth: birth, phoneNumber: phoneNumber }
            loginData('user', obj, nav, setUserInfo)
          }}
          btnText='로그인'
        >
          <InputBox label='휴대폰 번호' name='phoneNumber'>
            <PhoneInput name='phoneNumber' setState={setPhoneNumber} />
          </InputBox>
          <InputBox label='생년월일' name='birth'>
            <Input
              type='text'
              name='birth'
              placeholder='810101 (예시)'
              width='160px'
              onChange={changeInput}
              setState={setBirth}
              maxLength='6'
            />
          </InputBox>
        </Form>
        <Btn
          onClick={() => {
            nav('/signin')
          }}
          text='회원 가입'
          backGround='#595959'
        />
      </LoginBox>
      <Footer />
    </Page>
  )
}

const LoginBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 10px;
  padding: 0 0;
  font-family: 'Noto Sans KR', sans-serif;
`
