import styled from 'styled-components'
import Btn from './Btn'

export default function Form({ children, action, onSubmit, btnText }) {
  return (
    <FormBox
      action={action}
      onSubmit={(e) => {
        e.preventDefault()
        onSubmit(e)
      }}
    >
      {children}
      {btnText && (
        <div className='btnBox'>
          <Btn text={btnText} type='submit' />
        </div>
      )}
    </FormBox>
  )
}

const FormBox = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Noto Sans KR', sans-serif;

  .btnBox {
    display: flex;
    justify-content: center;
    align-items: center;
  }
`
