import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import BigBtn from '../../elements/BigBtn'
import H2 from '../../elements/H2'
import SelectBox from '../../elements/SelectBox'
import { classListData, getSelectDocData } from '../../firebase/firebase'

export default function ClassList({ userInfo }) {
  const nav = useNavigate()
  const [classList, setClassList] = useState([])
  const pickClass = (className) => {
    nav('/main/date')
    sessionStorage.setItem('pre_class', className)
  }
  useEffect(() => {
    if (userInfo.isClass) {
      nav('/main')
    } else if (userInfo.phoneNumber === '01099999999') {
      nav('/main/controller')
    } else if (userInfo.phoneNumber === '01077775228') {
      nav('/main/controller')
    } else {
      getSelectDocData('userClassData', 'name', userInfo.name, 'birth', Number(userInfo.birth))
        .then((doc) => {
          let classname = '22년 신규 교육'
          doc.forEach((item) => {
            classname = item.data().className
            return
          })
          sessionStorage.setItem('pre_class', classname)
          nav('/main/date')
        })
        .catch((e) => {
          console.error(e)
        })
    }
    classListData(setClassList)
  }, [nav, userInfo.birth, userInfo.isClass, userInfo.name, userInfo.phoneNumber])
  return (
    <SelectBox>
      <H2 text='교육을 선택해 주세요.' fontsize='1.1rem' margin='30px 0 0 0' />
      {classList.length === 0}
      {classList.map((item, index) => {
        return (
          <BigBtn
            onClick={() => {
              pickClass(item)
            }}
            text={item}
            key={`${item}${index}class`}
          />
        )
      })}
    </SelectBox>
  )
}
