import styled from 'styled-components'

const ListClassData = ({ classData, onClick, data }) => {
  return (
    <>
      <Head>
        <div className='date'>날짜</div>
        <div className='time'>시간</div>
        <div>수강인원</div>
        <div>마감여부</div>
      </Head>
      <Ul>
        {classData.map((item) => {
          return item.timeArr.map((time, index) => {
            const itemDate = item.date
            const itemTime = time.time
            const date = `${itemDate[4]}${itemDate[5]}/${itemDate[6]}${itemDate[7]}`
            const userTime = `${itemTime[0]}${itemTime[1]}:${itemTime[2]}${itemTime[3]}`
            const entryLength = time.entry.length
            const personnal = time.personnal
            return (
              <Li
                key={`data-${item.date}-${time.time}`}
                data-class={data}
                data-date={item.date}
                data-time={time.time}
                data-personnal={time.personnal}
                data-fewminute={time.fewMinute}
                data-index={index}
                onClick={onClick}
              >
                <div className='date'>{date}</div>
                <div className='time'>{userTime}</div>
                <div>{`${entryLength} / ${personnal}`}</div>
                {personnal < entryLength ? <div>마감</div> : <div></div>}
              </Li>
            )
          })
        })}
      </Ul>
    </>
  )
}

const Head = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  margin: 0 10px;
  font-size: 0.9rem;
  font-family: 'Noto Sans KR', sans-serif;

  div {
    padding: 10px;
    width: 100px;
    height: 20px;
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    &.time {
      width: 60px;
    }
    &.date {
      width: 60px;
    }
    &.belong {
      width: 80px;
    }
    &.name {
      width: 80px;
    }
    &.birth {
      @media screen and (max-width: 620px) {
        display: none;
      }
    }
    &.phoneNumber {
      width: 95px;
    }
    &.signInTime {
      width: 120px;
      @media screen and (max-width: 750px) {
        display: none;
      }
    }
    @media screen and (max-width: 620px) {
      padding: 5px;
    }
  }
`
const Ul = styled.ul`
  padding: 0 20px;
  margin: 0 10px;
`
const Li = styled.li`
  display: flex;
  cursor: pointer;
  font-size: 0.9rem;
  font-family: 'Noto Sans KR', sans-serif;

  div {
    padding: 10px;
    width: 100px;
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    &.time {
      width: 60px;
    }
    &.date {
      width: 60px;
    }
    &.belong {
      width: 80px;
      font-size: 14px;
    }
    &.name {
      width: 80px;
    }
    &.birth {
      @media screen and (max-width: 620px) {
        display: none;
      }
    }
    &.phoneNumber {
      width: 95px;
      font-size: 12px;
    }
    &.signInTime {
      width: 120px;
      font-size: 12px;
      @media screen and (max-width: 750px) {
        display: none;
      }
    }
    @media screen and (max-width: 620px) {
      padding: 5px;
    }
  }
`
export default ListClassData
