import { useRef } from 'react'
import styled from 'styled-components'

export default function PhoneInput({ name, setState, disabled }) {
  const baseNum = useRef()
  const frontNum = useRef()
  const backNum = useRef()
  const handlePhoneChange = (e) => {
    e.preventDefault()

    const base = baseNum.current.value
    const front = frontNum.current.value
    const back = e.currentTarget.value
    const num = `${base}${front}${back}`
    if (num.length >= 10) {
      setState(num)
    }
  }

  return (
    <InputBox>
      <input
        type='tel'
        placeholder='010'
        maxLength='3'
        onChange={(e) => {
          e.preventDefault()
          const num = e.target.value
          if (num.length === 3) {
            frontNum.current.focus()
          }
        }}
        // onFocus={(e) => {
        //   e.target.value = ''
        // }}
        defaultValue='010'
        id='basenum'
        ref={baseNum}
        disabled={disabled}
      />
      -
      <input
        type='tel'
        placeholder='0000'
        maxLength='4'
        onChange={(e) => {
          e.preventDefault()
          const num = e.target.value
          if (num.length === 4) {
            backNum.current.focus()
          }
        }}
        // onFocus={(e) => {
        //   e.target.value = ''
        // }}
        id='frontnum'
        ref={frontNum}
        disabled={disabled}
      />
      -
      <input
        type='tel'
        id={name}
        name={name}
        ref={backNum}
        placeholder='0000'
        maxLength='4'
        onChange={handlePhoneChange}
        // onFocus={(e) => {
        //   e.target.value = ''
        // }}
        disabled={disabled}
      />
    </InputBox>
  )
}

const InputBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Noto Sans KR', sans-serif;

  input {
    border: none;
    border-radius: 0;
    background-color: transparent;
    border-bottom: 2px solid #6e6e6e;
    font-size: 18px;
    text-align: center;
    width: 65px;
    margin: 0 5px;
  }
  .frontnum {
    margin-right: 10px;
    font-size: 20px;
    font-weight: 500;
  }
`
