import { useEffect, useState } from 'react'
import SelectBox from '../../elements/SelectBox'
import { classIdListData } from '../../firebase/firebase'
import H2 from '../../elements/H2'
import { useNavigate } from 'react-router-dom'
import BigBtn2 from '../../elements/BigBtn2'

export default function DeleteClass() {
  const nav = useNavigate()
  const [classList, setClassList] = useState([])
  useEffect(() => {
    classIdListData(setClassList)
  }, [])
  console.log(classList)
  const handleDeleteClick = (e) => {
    const classname = e.currentTarget.name
    const { classid } = e.currentTarget.dataset
    sessionStorage.setItem('delete_class', classname)
    sessionStorage.setItem('delete_classId', classid)
    nav('/main/deleteclassdate')
  }
  return (
    <SelectBox>
      <H2 text='삭제할 교육을 선택해 주세요.' fontsize='1.1rem' margin='30px 0 0 0' />
      {classList.length === 0}
      {classList.map((item, index) => {
        return (
          <BigBtn2
            onClick={handleDeleteClick}
            text={item.className}
            key={`${item.className}${index}class`}
            classId={item.classId}
          />
        )
      })}
    </SelectBox>
  )
}
