import { useState } from "react";
import { Route, Routes } from "react-router-dom";
import ChoiceData from "../data/ChoiceData";
import DataList from "../data/DataList";
export default function ClassData() {
  const [data, setData] = useState("");
  return (
    <Routes>
      <Route
        path="/"
        element={<ChoiceData data={data} setData={setData} />}
      ></Route>
      <Route
        path="/datalist"
        element={<DataList data={data} setData={setData} />}
      ></Route>
    </Routes>
  );
}
