import styled from 'styled-components'

export default function Btn({ text, onClick, type, backGround }) {
  return (
    <BtnEl type={type} onClick={onClick} backGround={backGround}>
      {text}
    </BtnEl>
  )
}

const BtnEl = styled.button`
  display: block;
  border: 3px solid #9d9d9d;
  border-radius: 10px;
  padding: 10px;
  font-family: 'Noto Sans KR', sans-serif;

  background-color: ${(props) => {
    if (props.backGround) {
      return props.backGround
    } else {
      return 'white'
    }
  }};
  cursor: pointer;
  width: 300px;
  color: ${(props) => {
    if (props.backGround) {
      return 'white'
    } else {
      return '#262626'
    }
  }};
  font-size: 20px;
  box-sizing: border-box;
  margin-top: 5px;
  z-index: 2;
  margin-bottom: 5px;
  :hover {
    border: 3px solid black;
    color: ${(props) => {
      if (props.backGround) {
        return props.backGround
      } else {
        return '#262626'
      }
    }};
    background-color: white;
    font-weight: 600;
  }
`
