export const signInUserData = async (info, nav, setIsWaiting) => {
  const phoneNumber = info.phoneNumber
  const q = query(collection(db, 'user'), where('phoneNumber', '==', phoneNumber))
  const querySnapshot = await getDocs(q)
  let existUser = false
  querySnapshot.forEach((doc) => {
    existUser = true
  })
  if (existUser) {
    alert('이미 가입된 번호입니다. 로그인해주세요.')
    nav('/')
  } else {
    /* 3. 콜백 함수 정의하기 */
    const callback = (response) => {
      const { success, error_msg } = response
      if (success) {
        setIsWaiting(false)
        nav(`/signin?imp_uid=${response.imp_uid}`)
      } else {
        alert(`본인인증 실패: ${error_msg}`)
      }
    }
    const certification = () => {
      /* 1. 가맹점 식별하기 */
      const { IMP } = window
      IMP.init('imp75992285')
      const host = window.location.host
      /* 2. 본인인증 데이터 정의하기 */
      const data = {
        merchant_uid: `mid_${new Date().getTime()}`,
        m_redirect_url: `${host}/signin`,
      }

      /* 4. 본인인증 창 호출하기 */
      IMP.certification(data, callback)
    }
    certification(info)
  }
}

