export const addClassData = async (collectionName, date, timeobj, classId, isAdd, setIsLoding) => {
  try {
    const docRef = doc(db, collectionName, date)
    const docSnap = await getDoc(docRef)
    const querySnapshot = await getDocs(collection(db, 'classList'))
    let classList = []
    querySnapshot.forEach((doc) => {
      classList.push(doc.data().className)
    })
    if (!classList.includes(collectionName)) {
      await setDoc(doc(db, 'classList', classId), {
        classId: classId,
        className: collectionName,
      })
    }
    if (docSnap.exists()) {
      const docData = docSnap.data()
      let isTime = false
      // eslint-disable-next-line array-callback-return
      let timeObj = {}
      let timeNum = 0
      let changeClass = false
      docData.timeArr.forEach((item, index) => {
        if (timeobj.time === item.time) {
          isTime = true
          timeObj = { ...item }
          timeNum = index
          if (isAdd) {
            alert('해당 날짜에 같은 시간 수업이 이미 존재합니다. 다른 시간을 선택해주세요.')
            setIsLoding(true)
            return
          } else {
            changeClass = window.confirm(
              `(교육인원:${timeObj.personnal}->${timeobj.personnal})(교육시간:${timeObj.fewMinute}->${timeobj.fewMinute})으로 수정됩니다. 수정하시겠습니까?`
            )
          }
        }
      })
      if (!isTime && isAdd) {
        const newTimeArr = [...docData.timeArr, timeobj]
        newTimeArr.sort(function (a, b) {
          return a.time - b.time
        })
        try {
          await setDoc(doc(db, collectionName, date), {
            date: date,
            timeArr: newTimeArr,
          })
          alert('수업이 성공적으로 추가 되었습니다.')
          setIsLoding(true)
        } catch (e) {
          console.error('해당날짜에 시간 추가를 시도 했지만 실패하였습니다.')
          setIsLoding(true)
        }
      } else {
        if (changeClass) {
          timeObj = {
            ...timeObj,
            personnal: timeobj.personnal,
            fewMinute: timeobj.fewMinute,
          }
          let newTimeArr = [...docData.timeArr]
          newTimeArr[timeNum] = timeObj
          try {
            await setDoc(doc(db, collectionName, date), {
              date: date,
              timeArr: newTimeArr,
            })
            alert('수업이 성공적으로 수정 되었습니다.')
            setIsLoding(true)
          } catch (e) {
            console.error('해당날짜에 시간 수정을 시도 했지만 실패하였습니다.')
            setIsLoding(true)
          }
        } else {
          alert('수업이 존재하지 않습니다. 수업을 추가 후 수정해주세요.')

          setIsLoding(true)
          return
        }
      }
    } else {
      // doc.data() will be undefined in this case
      if (isAdd) {
        try {
          await setDoc(doc(db, collectionName, date), {
            date: date,
            timeArr: [timeobj],
          })
          alert('수업이 성공적으로 추가 되었습니다.')
          setIsLoding(true)
          // setUserInfo((data) => ({ ...data, isMaster: false }));
          // nav("/main/controll");
        } catch (e) {
          console.error('새로운 수업일에 시간을 추가하는것을 실패 했습니다.')
          setIsLoding(true)
        }
      } else {
        alert('수업이 존재하지 않습니다. 수업을 추가 후 수정해주세요.')
        setIsLoding(true)
      }
    }
  } catch (e) {
    console.error('시도도 못하고 실패')
    setIsLoding(true)
    console.error(e)
  }
}

export const confirmClassData = async (collectionName, date, time, setUserInfo, userInfo) => {
  try {
    const docRef = doc(db, collectionName, date)
    const docSnap = await getDoc(docRef)
    if (docSnap.exists()) {
      const docData = docSnap.data()
      // eslint-disable-next-line array-callback-return
      let isClass = false
      docData.timeArr.forEach((item) => {
        if (time === item.time) {
          isClass = true
        }
      })
      if (!isClass) {
        setUserInfo((prev) => ({
          ...prev,
          class: '',
          isClass: false,
          signInTime: '',
        }))
        const user = {
          ...userInfo,
          class: '',
          isClass: false,
          signInTime: '',
        }
        try {
          await setDoc(doc(db, 'user', user.id), user)
          alert('이전에 신청했던 교육이 취소 되었습니다. 다시 신청해 주세요.')
        } catch (e) {
          console.error('Error adding document: ', e)
        }
      }
    } else {
      setUserInfo((prev) => ({
        ...prev,
        class: '',
        isClass: false,
        signInTime: '',
      }))
      const user = {
        ...userInfo,
        class: '',
        isClass: false,
        signInTime: '',
      }
      try {
        await setDoc(doc(db, 'user', user.id), user)
        alert('이전에 신청했던 교육이 취소 되었습니다. 다시 신청해 주세요.')
      } catch (e) {
        console.error('Error adding document: ', e)
      }
    }
  } catch (e) {
    console.error('클래스 확인 시도도 못하고 실패')
    console.error(e)
  }
}

export const applyConfirmClassData = async (
  pre_class,
  pre_date,
  pre_time,
  pre_fewMinute,
  userInfo,
  setUserInfo,
  setShowBtn,
  nav
) => {
  try {
    const docRef = doc(db, pre_class, pre_date)
    const docSnap = await getDoc(docRef)
    if (docSnap.exists()) {
      const docData = docSnap.data()
      // eslint-disable-next-line array-callback-return
      let isUser = false
      docData.timeArr.forEach((item) => {
        if (pre_time === item.time) {
          item.entry.forEach((item) => {
            if (item.phoneNumber === userInfo.phoneNumber) {
              isUser = true
            }
          })
        }
      })
      if (!isUser) {
        promiseAddClassnuserData(pre_class, pre_date)
          .then((res) => {
            if (res.exists()) {
              const timeData = res.data()
              const timearr = timeData.timeArr.map((item) => {
                if (item.time === pre_time) {
                  item.entry = [...item.entry, userInfo]
                  return item
                } else {
                  return item
                }
              })
              const timeobj = {
                date: pre_date,
                timeArr: timearr,
              }
              setClassData(pre_class, pre_date, timeobj)
              const obj = {
                ...userInfo,
                class: `${pre_class}/${pre_date}/${pre_time}/${pre_fewMinute}`,
                isClass: true,
              }
              setUserInfo(obj)
              setUserData('user', obj)
              setShowBtn(false)
              nav('/main')
            }
          })
          .catch((e) => {
            console.error(e)
          })
      } else {
        nav('/main')
      }
    }
  } catch (e) {
    console.error('클래스 확인 시도도 못하고 실패')
    console.error(e)
  }
}

export const deleteClassData = async (collectionName, date, timeobj, classNameList, setClassNameList) => {
  try {
    const docSnap = await getDoc(doc(db, collectionName, date))
    if (docSnap.exists()) {
      const docData = docSnap.data()
      let isTime = false

      const newDocData = docData.timeArr.filter((item) => {
        if (timeobj.time === item.time) {
          isTime = true
        }
        return timeobj.time !== item.time
      })

      if (isTime) {
        const querySnapshot = await getDocs(collection(db, collectionName))
        const classDateArr = []
        querySnapshot.forEach((doc) => {
          classDateArr.push(doc.data())
        })
        if (classDateArr.length !== 1) {
          // 해당 교육 프로그램에 "교육일"이 마지막이 아니라면
          if (newDocData.length === 0) {
            //교육일에 "교육타임"이 마지막 하나였다면
            await deleteDoc(doc(db, collectionName, date))
            alert('수업이 성공적으로 삭제 되었습니다.')
          } else {
            //교육일에 "교육타임"이 마지막이 아니라면
            try {
              await setDoc(doc(db, collectionName, date), {
                date: date,
                timeArr: [...newDocData],
              })
              alert('수업이 성공적으로 삭제 되었습니다.')
            } catch (e) {
              alert('수업이 삭제 실패 다시 시도해 주세요.')
              console.error('해당수업 삭제 실패')
              console.error(e)
            }
          }
        } else {
          // 해당 교육 프로그램에 "교육일"이 마지막이라면
          if (newDocData.length === 0) {
            //교육일에 "교육타임"이 마지막 하나였다면
            const isConfirm = window.confirm(
              '해당교육시간이 마지막 교육시간입니다. 해당 교육시간을 삭제하면 교육이 삭제 됩니다. 삭제하시겠습니까? '
            )
            if (isConfirm) {
              const q = query(collection(db, 'classList'), where('className', '==', collectionName))
              const querySnapshot = await getDocs(q)
              let classId = 0
              querySnapshot.forEach((doc) => {
                // doc.data() is never undefined for query doc snapshots
                classId = doc.data().classId
              })
              await deleteDoc(doc(db, 'classList', classId))
              await deleteDoc(doc(db, collectionName, date))
              let newList = classNameList
              const listIndex = classNameList.indexOf(collectionName)
              if (listIndex > -1) {
                newList.splice(listIndex, 1)
                setClassNameList(newList)
              }
              alert('수업이 성공적으로 삭제 되었습니다.')
            }
          } else {
            //교육일에 "교육타임"이 마지막이 아니라면
            try {
              await setDoc(doc(db, collectionName, date), {
                date: date,
                timeArr: [...newDocData],
              })
              alert('수업이 성공적으로 삭제 되었습니다.')
            } catch (e) {
              alert('수업이 삭제 실패 다시 시도해 주세요.')
              console.error('해당수업 삭제 실패')
              console.error(e)
            }
            alert('수업이 성공적으로 삭제 되었습니다.')
          }
        }
      } else {
        alert('존재하지 않는 수업 입니다.')
      }
    } else {
      alert('해당 수업 프로그램이 존재하지 않습니다.')
    }
  } catch (e) {
    console.error('시도도 못하고 실패')
  }
}

