export const loginData = async (collectionName, info, nav, setUserInfo) => {
  try {
    const phoneNumber = info.phoneNumber
    const q = query(collection(db, collectionName), where('phoneNumber', '==', phoneNumber))
    const querySnapshot = await getDocs(q)
    let isPhone = false
    let isBirth = false
    querySnapshot.forEach((doc) => {
      const item = doc.data()
      let isClass = false
      if (String(item.phoneNumber) === String(info.phoneNumber)) {
        isPhone = true
        if (String(item.birth) === String(info.birth)) {
          isBirth = true
          if (item.class) {
            isClass = true
          } else {
            isClass = false
          }
          setUserInfo({
            ...item,
            isClass: isClass,
          })
          nav('/main')
        }
      }
    })
    if (!isPhone) {
      alert('등록되지 않은 번호 입니다.')
    } else {
      if (!isBirth) {
        alert('생년월일을 바르게 입력해 주세요.')
      }
    }
  } catch (e) {
    console.error('Error adding document: ', e)
  }
}

export const signInConfirmData = async (setData, collectionName, info, setIsDisabled, setIsWaiting, setInfo) => {
  try {
    const querySnapshot = await getDocs(collection(db, collectionName))
    let isNumber = false
    querySnapshot.forEach((doc) => {
      const item = doc.data()
      if (String(item.phoneNumber) === String(info.phoneNumber)) {
        isNumber = true
        setInfo((prev) => ({ ...prev, id: item.id }))
      }
    })
    if (!isNumber) {
      setIsWaiting(false)
      alert('등록되지 않은 번호 입니다.')
    } else {
      const requestOTP = (info, setData) => {
        //인증 문자 보내기
        const phoneNumber = `+82${info.phoneNumber.slice(1)}`
        if (phoneNumber.length >= 12) {
          generateRecaptcha()
          const appVerifier = window.recaptchaVerifier
          signInWithPhoneNumber(auth, phoneNumber, appVerifier)
            .then((confirmationResult) => {
              window.confirmationResult = confirmationResult
              setIsWaiting(false)
              setData(true)
            })
            .catch((error) => {
              // Error; SMS not sent
              setIsWaiting(false)
              console.error(error)
            })
        }
      }

      requestOTP(info, setData)
    }
  } catch (e) {
    console.error('Error adding document: ', e)
  }
}

export const readCollectionData = async (collectionName, setData, startClassDate, endClassDate) => {
  try {
    const q = query(
      collection(db, collectionName),
      where('date', '>=', startClassDate),
      where('date', '<=', endClassDate)
    )
    const querySnapshot = await getDocs(q)
    let dataArr = []
    querySnapshot.forEach((doc) => {
      dataArr.push(doc.data())
    })
    dataArr.sort(function (a, b) {
      return a.date - b.date
    })
    setData(dataArr)
  } catch (e) {
    console.error(e)
  }
}

