export const searchUserData = async (phone, setInfo, setShow) => {
  const collectionRef = collection(db, 'user')
  const q = query(collectionRef, where('phoneNumber', '==', phone))
  const querySnapshot = await getDocs(q)
  const userData = []
  querySnapshot.forEach((doc) => {
    userData.push(doc.data())
  })
  if (userData.length > 0) {
    setInfo(() => userData[0])
  } else {
    setShow(true)
    setInfo(() => ({ name: '' }))
  }
}

export const deleteUserClassData = async (collection, docId, time, phoneNumber) => {
  try {
    const docRef = doc(db, collection, docId)
    const docSnap = await getDoc(docRef)

    if (docSnap.exists()) {
      const newTimeArr = docSnap.data().timeArr.filter((item) => {
        if (item.time === time) {
          item.entry = item.entry.filter((user) => {
            return user.phoneNumber !== phoneNumber
          })
          return item
        } else {
          return item
        }
      })
      const newData = { date: docId, timeArr: newTimeArr }
      try {
        await setDoc(doc(db, collection, docId), newData)
      } catch (e) {
        console.error('deleteUserClassData :' + e)
        alert('교육 정보에서 유저의 신청 정보를 삭제하지 못했습니다.')
      }
    } else {
      alert('교육 정보가 존재하지 않습니다.')
    }
  } catch (e) {
    console.error('deleteUserClassData :' + e)
    alert('교육 정보에서 유저의 신청 정보를 얻어오지 못했습니다.')
  }
}

export const userClassAlterData = async (classname, date, time, userClass, info, setInfo, setIsLoding, later) => {
  const collectionRef = collection(db, classname)
  const q = query(collectionRef, where('date', '==', date))
  try {
    const querySnapshot = await getDocs(q)
    let isDate = false
    let dateInfo
    querySnapshot.forEach((doc) => {
      isDate = true
      if (doc.data().timeArr) {
        dateInfo = { ...doc.data() }
      }
    })
    if (isDate) {
      let fewMinute
      let isTime = false
      const newTimeArrInfo = dateInfo.timeArr.map((item) => {
        if (item.time === time) {
          item.entry.push(info)
          fewMinute = item.fewMinute
          isTime = true
          return item
        } else {
          return item
        }
      })
      if (isTime) {
        dateInfo.timeArr = newTimeArrInfo
        const newUserClass = `${userClass}/${fewMinute}`
        const newUserData = { ...info, class: newUserClass, isClass: true }
        setInfo((prev) => ({ ...prev, class: newUserClass, isClass: true }))
        // // 기존 교육 정보에서 유저 삭제
        const beforeClass = info.class
        const beforeClassName = beforeClass.split('/')[0]
        const beforeClassDate = beforeClass.split('/')[1]
        const beforeClassTime = beforeClass.split('/')[2]
        deleteUserClassData(beforeClassName, beforeClassDate, beforeClassTime, info.phoneNumber)
        // 교육 정보에 유저 추가
        await setDoc(doc(db, classname, date), dateInfo)
        // 유저 정보에 교육 추가
        await setDoc(doc(db, 'user', info.id), newUserData)
        //22년 신규교육/202206024/0900/60 => 22년 신규교육2022060720900
        const laterClass = later.split('/')
        const laterClassName = laterClass[0]
        const laterClassDate = laterClass[1]
        const laterClassTime = laterClass[2]
        alert(
          `${laterClassName} -> ${classname}
${laterClassDate.slice(0, 4)}년 ${laterClassDate.slice(4, 6)}월 ${laterClassDate.slice(6, 8)}일 -> ${date.slice(
            0,
            4
          )}년 ${date.slice(4, 6)}월 ${date.slice(6, 8)}일
${laterClassTime.slice(0, 2)}시${laterClassTime.slice(2, 4)}분 -> ${time.slice(0, 2)}시${time.slice(2, 4)}분
변경이 완료 되었습니다.`
        )
      } else {
        setIsLoding(true)
        alert('해당 교육이 존재하지 않습니다.')
      }
    } else {
      setIsLoding(true)
      alert('해당 교육이 존재하지 않습니다.')
    }
  } catch (e) {
    console.error(e)
    setIsLoding(true)
    alert('페이지를 닫고 다시 시도해 주세요.')
  }
}
