import styled from 'styled-components'

export default function SignInInput({
  type,
  name,
  placeholder,
  width,
  onChange,
  setState,
  disabled,
  maxLength,
  value,
}) {
  return (
    <InputEl
      type={type}
      name={name}
      id={name}
      width={width}
      placeholder={placeholder}
      onChange={(e) => onChange(e, setState)}
      maxLength={maxLength}
      disabled={disabled}
      value={value}
    ></InputEl>
  )
}

const InputEl = styled.input`
  text-align: center;
  border: none;
  border-radius: 0;
  border-bottom: 2px solid #6e6e6e;
  font-size: 1.25rem;
  margin: 10px 0;
  background-color: transparent;
  width: ${(props) => {
    return props.width
  }};
  color: black;
`
