import styled from 'styled-components'

const ListData = ({ classData }) => {
  return (
    <>
      <Head>
        <div className='date'>날짜</div>
        <div className='time'>시간</div>
        <div className='belong'>소속</div>
        <div className='name'>이름</div>
        <div className='birth'>생년월일</div>
        <div className='phoneNumber'>핸드폰번호</div>
        <div className='signInTime'>예약등록일시</div>
      </Head>
      <Ul>
        {classData.map((item) => {
          return item.timeArr.map((time) => {
            const itemDate = item.date
            const itemTime = time.time
            const date = `${itemDate[4]}${itemDate[5]}/${itemDate[6]}${itemDate[7]}`
            const userTime = `${itemTime[0]}${itemTime[1]}:${itemTime[2]}${itemTime[3]}`
            return (
              time.entry.length > 0 &&
              time.entry.map((user, userIndex) => {
                const key = `data-${item.date}-${time.time}-${userIndex}`
                return (
                  <Li key={key}>
                    <div className='date'>{date}</div>
                    <div className='time'>{userTime}</div>
                    <div className='belong'>{user.belong}</div>
                    <div className='name'>{user.name}</div>
                    <div className='birth'>{user.birth}</div>
                    <div className='phoneNumber'>{user.phoneNumber}</div>
                    <div className='signInTime'>{user.signInTime}</div>
                  </Li>
                )
              })
            )
          })
        })}
      </Ul>
    </>
  )
}

const Head = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  margin: 0 10px;
  font-size: 0.9rem;
  font-family: 'Noto Sans KR', sans-serif;

  div {
    padding: 10px;
    width: 100px;
    height: 20px;
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    &.time {
      width: 60px;
    }
    &.date {
      width: 60px;
    }
    &.belong {
      width: 80px;
      @media screen and (max-width: 400px) {
        display: none;
      }
    }
    &.name {
      width: 80px;
    }
    &.birth {
      @media screen and (max-width: 620px) {
        display: none;
      }
    }
    &.phoneNumber {
      width: 95px;
    }
    &.signInTime {
      width: 130px;
      @media screen and (max-width: 750px) {
        display: none;
      }
    }
    @media screen and (max-width: 620px) {
      padding: 5px;
    }
  }
`
const Ul = styled.ul`
  padding: 0 20px;
  margin: 0 10px;
`
const Li = styled.li`
  display: flex;
  font-size: 0.9rem;
  font-family: 'Noto Sans KR', sans-serif;

  div {
    padding: 10px;
    width: 100px;
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    &.time {
      width: 60px;
    }
    &.date {
      width: 60px;
    }
    &.belong {
      width: 80px;
      font-size: 14px;
      @media screen and (max-width: 400px) {
        display: none;
      }
    }
    &.name {
      width: 80px;
    }
    &.birth {
      @media screen and (max-width: 620px) {
        display: none;
      }
    }
    &.phoneNumber {
      width: 95px;
      font-size: 12px;
    }
    &.signInTime {
      width: 130px;
      font-size: 12px;
      @media screen and (max-width: 750px) {
        display: none;
      }
    }
    @media screen and (max-width: 620px) {
      padding: 5px;
    }
  }
`
export default ListData
