import styled from "styled-components";

export default function Select({
  name,
  onChange,
  selectArr,
  width,
  fontSize,
  defaultvalue,
}) {
  return (
    <SelectEl
      name={name}
      id={name}
      onChange={onChange}
      width={width}
      fontSize={fontSize}
    >
      {selectArr.map((item, index) => {
        if (defaultvalue === item) {
          return (
            <option value={item} key={`${item}${index}`} selected>
              {item}
            </option>
          );
        } else {
          return (
            <option value={item} key={`${item}${index}`}>
              {item}
            </option>
          );
        }
      })}
    </SelectEl>
  );
}

const SelectEl = styled.select`
  text-align: center;
  border: none;
  border-radius: 0;
  background-color: transparent;
  border-bottom: 2px solid #6e6e6e;
  font-size: ${(props) => {
    if (props.fontSize) {
      return props.fontSize;
    } else {
      return "20px";
    }
  }};
  margin: 10px 0;
  width: ${(props) => {
    return props.width;
  }};
  padding: 10px 0;
`;
