import { useNavigate } from 'react-router-dom'
import { useEffect } from 'react'
import Btn from '../../elements/Btn'
import styled from 'styled-components'
import { DAY } from '../../utils/constant'

export default function UserInfoAdmin({ info, setDisable, show, setShow }) {
  const nav = useNavigate()
  const classDate = (classData) => {
    const classDate = classData.split('/')[1]
    const year = `${classDate[0]}${classDate[1]}${classDate[2]}${classDate[3]}`
    const month = `${classDate[4]}${classDate[5]}`
    const date = `${classDate[6]}${classDate[7]}`
    const day = DAY[Number(`${classDate[8]}`)]
    return `${year}년${month}월${date}일${day}요일`
  }
  const classTime = (classData) => {
    const classDate = classData.split('/')[2]
    const hour = `${classDate[0]}${classDate[1]}`
    const minute = `${classDate[2]}${classDate[3]}`
    return `${hour}:${minute}`
  }
  useEffect(() => {
    setDisable(false)
  }, [setDisable])
  const handleClick = () => {
    nav('/main/useradmin/alterclass')
  }
  return (
    <div>
      {info.name !== '' ? (
        <>
          <InfoUl>
            <InfoLi>
              <Menu>이름 :</Menu>
              {info.name}
            </InfoLi>
            <InfoLi>
              <Menu>생일 :</Menu>
              {info.birth}
            </InfoLi>
            <InfoLi>
              <Menu>소속 :</Menu>
              {info.belong}
            </InfoLi>
            {info.isClass ? (
              <>
                <InfoLi>
                  <Menu>신청교육 :</Menu>
                  {info.class.split('/')[0]}
                </InfoLi>
                <InfoLi>
                  <Menu>교육일 :</Menu>
                  {classDate(info.class)}
                </InfoLi>
                <InfoLi>
                  <Menu>교육시간 :</Menu>
                  {classTime(info.class)}
                </InfoLi>
                <InfoLi>
                  <Menu>신청시간 :</Menu>
                  {info.signInTime}
                </InfoLi>
              </>
            ) : (
              <InfoLi>교육을 신청하지 않았습니다.</InfoLi>
            )}
            <Btn text='교육 정보 변경' onClick={handleClick} type='button' />
          </InfoUl>
        </>
      ) : (
        show && <div> 검색 결과 없음 </div>
      )}
    </div>
  )
}

const InfoUl = styled.ul`
  width: 300px;
  font-size: 0.95rem;
`
const InfoLi = styled.li`
  margin-bottom: 10px;
`
const Menu = styled.span`
  margin-right: 10px;
  font-weight: 600;
  font-size: 1rem;
`
