import { useState, useEffect } from 'react'
import { addUserData, signInUserData } from '../firebase/firebase'
import InputBox from '../elements/InputBox'
import PhoneInput from '../elements/PhoneInput'
import Select from '../elements/Select'
import Btn from '../elements/Btn'
import { useNavigate, useSearchParams } from 'react-router-dom'
import BackBtn from '../elements/BackBtn'
import Page from '../elements/Page'
import Form from '../elements/Form'
import Header from '../elements/Header'
import { belongArr } from '../store/Data'
import PageLogo from '../elements/PageLogo'
import styled from 'styled-components'
import { res } from '../firebase/api'
import Waiting from '../elements/Waiting'
import Footer from '../elements/Footer'
import SignInInput from '../elements/SignInInput'

export default function Signin() {
  const today = new Date()
  const userInfo = {
    name: '',
    birth: '',
    belong: '선택',
    phoneNumber: '',
    class: '',
    id: `${today.getFullYear()}${
      Number(today.getMonth()) + 1
    }${today.getDate()}${today.getHours()}${today.getMinutes()}${today.getSeconds()}${today.getMilliseconds()}`,
    agree: 'false',
  }
  const [info, setInfo] = useState(userInfo)
  const [check, setCheck] = useState(true)
  const [isWaiting, setIsWaiting] = useState(false)
  const nav = useNavigate()
  const [searchParams] = useSearchParams()
  const [success, setSuccess] = useState(false)
  useEffect(() => {
    const imp_uid = searchParams.get('imp_uid')
    if (imp_uid) {
      setIsWaiting(true)
      res(imp_uid, setSuccess, setInfo, setIsWaiting, nav)
    }
  }, [nav, searchParams])
  const handleBelongChange = (e) => {
    const targetName = e.currentTarget.name
    const targetValue = e.currentTarget.value
    setInfo((prev) => ({ ...prev, [targetName]: targetValue }))
  }
  const handleCheckClick = (e) => {
    const isAgree = e.currentTarget.dataset.ischeck
    setCheck((prev) => !prev)
    setInfo((prev) => ({
      ...prev,
      agree: isAgree,
    }))
  }
  const handleCertificationClick = () => {
    if (info.phoneNumber.length >= 10) {
      setIsWaiting(true)
      signInUserData(info, nav, setIsWaiting)
    } else {
      if (info.phoneNumber.length < 10) {
        alert('핸드폰 번호를 모두 입력해 주세요.')
      } else {
        alert('제대로 입력되지 않았습니다.')
      }
    }
  }
  const handlePhoneInputChange = (num) => {
    setInfo((prev) => ({
      ...prev,
      phoneNumber: num,
    }))
  }

  const handleSubmitClick = (e) => {
    e.preventDefault()
    if (
      info.belong !== '선택' &&
      info.name !== '' &&
      info.agree === 'true' &&
      info.birth !== '' &&
      info.phoneNumber.length >= 10
    ) {
      setIsWaiting(true)
      addUserData('user', info, nav, setIsWaiting)
    } else {
      if (info.agree !== 'true') {
        alert('개인정보 제공에 동의해 주세요.')
      } else if (info.belong === '선택') {
        alert('소속을 선택해 주세요.')
      } else {
        alert('제대로 입력되지 않았습니다.')
      }
    }
  }

  return (
    <Page>
      <Header>
        <BackBtn top='0px' left='0px' />
      </Header>
      <PageLogo
        height='170px'
        text='회원가입 후 로그인하여 이용해주세요.'
        text2={success ? '서비스이용을 위해 본인인증 해주세요.' : '개인정보제공에 동의하고, 소속을 선택해주세요.'}
      />
      {isWaiting ? (
        <Waiting />
      ) : (
        <SignInBox>
          <Form action='submit'>
            {success ? (
              <>
                <AgreeBox>
                  <p className='bold'>개인정보 수집동의(필수) :</p>
                  <p>경기교통 친절교육 서비스 회원가입을 위해 아래와 같이 개인정보를 수집·이용합니다.</p>
                  <p className='small'>수집 목적 : 회원식별 및서비스 제공</p>
                  <p className='small'>수집 항목 : 이름, 생년월일, 연락처, 소속</p>
                  <p className='small'>보유·이용 기간: 이용목적 달성 시</p>
                  <p>※ 동의를 거부할 권리가 있습니다. 다만, 필수 동의 거부 시 교육 신청이 제한될 수 있습니다.</p>
                </AgreeBox>
                <AgreeCheckBox>
                  <input
                    type='checkbox'
                    name='agree'
                    data-ischeck={check}
                    onClick={handleCheckClick}
                    style={{ marginTop: '12px' }}
                  />
                  <label style={{ fontSize: '13px', marginTop: '10px' }}>위 개인정보 수집·이용에 동의합니다.</label>
                </AgreeCheckBox>
                <InputBox name='name' label='이름' src={info.name !== '' ? true : false} alt='CHECKIMAGE'>
                  <SignInInput name='name' value={info.name} width='140px' disabled={true} />
                </InputBox>
                <InputBox name='birth' label='생년월일' src={info.birth !== '' ? true : false} alt='CHECKIMAGE'>
                  <SignInInput name='birth' value={info.birth} width='140px' disabled={true} maxLength='6' />
                </InputBox>
                <InputBox
                  name='phoneNumber'
                  label='휴대폰 번호'
                  src={info.birth !== '' ? true : false}
                  alt='CHECKIMAGE'
                >
                  <SignInInput name='birth' value={info.phoneNumber} width='140px' disabled={true} />
                </InputBox>
                <InputBox name='belong' label='소속' src={info.belong !== '선택' ? true : false} alt='CHECKIMAGE'>
                  <Select name='belong' onChange={handleBelongChange} selectArr={belongArr} width='auto'></Select>
                </InputBox>
                <Btn text='가입 신청' type='submit' onClick={handleSubmitClick} />
              </>
            ) : (
              <>
                <InputBox name='phoneNumberInput' label='휴대폰번호' alt='CHECKIMAGE'>
                  <PhoneInput name='phoneNumber' setState={handlePhoneInputChange} />
                </InputBox>

                <Btn text='본인인증' type='button' onClick={handleCertificationClick} />
              </>
            )}
          </Form>
        </SignInBox>
      )}
      <Footer />
    </Page>
  )
}

const AgreeBox = styled.div`
  font-size: 14px;
  padding: 5px;
  width: 300px;
  height: 60px;
  border: 3px solid #e6e6e6;
  border-radius: 5px;
  overflow: scroll;
  p {
    margin: 2px 0;
  }
  .bold {
    font-weight: 700;
  }
  .small {
    font-size: 12px;
  }
`

const AgreeCheckBox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 220px;
`
const SignInBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 10px;
  padding: 10px 0;
  font-family: 'Noto Sans KR', sans-serif;
`
