import axios from 'axios'
export const res = (imp_uid, setSuccess, setInfo, setIsWaiting, nav) => {
  axios({
    url: 'https://us-central1-class-registration-valueup.cloudfunctions.net/certifications', // 예: https://www.myservice.com/certifications
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    data: { imp_uid: imp_uid },
  })
    .then(function a(res) {
      const birth = `${res.data.birthday[2]}${res.data.birthday[3]}${res.data.birthday[5]}${res.data.birthday[6]}${res.data.birthday[8]}${res.data.birthday[9]}`
      const info = {
        name: res.data.name,
        birth: birth,
        phoneNumber: res.data.phoneNumber,
      }
      setInfo((prev) => ({ ...prev, ...info }))
      setSuccess(true)
      setIsWaiting(false)
    })
    .catch(function (error) {
      console.error(error)
      console.log('회원가입 실패')
      alert('회원가입에 실패 하였습니다. 다시 시도해주세요.')
      nav('/')
    })
}
