import { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import Btn from '../../elements/Btn'
import MyClass from '../../elements/MyClass'
import SelectBox from '../../elements/SelectBox'
import { applyConfirmClassData, promiseAddClassnuserData, setClassData, setUserData } from '../../firebase/firebase'

export default function Result({ userInfo, setUserInfo, setShowBtn }) {
  const nav = useNavigate()
  const pre_class = sessionStorage.getItem('pre_class')
  const pre_date = sessionStorage.getItem('pre_date')
  const pre_time = sessionStorage.getItem('pre_time')
  const pre_fewMinute = sessionStorage.getItem('pre_fewMinute')

  const handleResultSubmitClick = () => {
    const resultConfirm = window.confirm('교육 신청 내용을 모두 확인하였으며, 해당 교육을 신청하시겠습니까?')
    resultConfirm &&
      // addClassnuserData(pre_class, pre_date, pre_time, pre_fewMinute, userInfo, setUserInfo, setShowBtn, nav)
      // addClassnuserData(collection, id, time, fewMinute, userInfo, setUserInfo, setShowBtn, nav)
      promiseAddClassnuserData(pre_class, pre_date)
        .then((res) => {
          if (res.exists()) {
            const timeData = res.data()
            const timearr = timeData.timeArr.map((item) => {
              if (item.time === pre_time) {
                item.entry = [...item.entry, userInfo]
                return item
              } else {
                return item
              }
            })
            const timeobj = {
              date: pre_date,
              timeArr: timearr,
            }
            setClassData(pre_class, pre_date, timeobj)
            const obj = {
              ...userInfo,
              class: `${pre_class}/${pre_date}/${pre_time}/${pre_fewMinute}`,
              isClass: true,
            }
            setUserInfo(obj)
            setUserData('user', obj)
            setShowBtn(false)
            nav('/main')
          }
        })
        .catch((e) => {
          console.error(e)
        })
  }
  useEffect(() => {
    const today = new Date()

    const signInTime = `${today.getFullYear()}-${
      today.getMonth() + 1
    }-${today.getDate()} ${today.getHours()}:${today.getMinutes()}:${today.getSeconds()}`
    setUserInfo((prev) => ({ ...prev, signInTime: signInTime }))
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    if (userInfo.isClass) {
      applyConfirmClassData(pre_class, pre_date, pre_time, pre_fewMinute, userInfo, setUserInfo, setShowBtn, nav)
    }
  }, [nav, pre_class, pre_date, pre_fewMinute, pre_time, setShowBtn, setUserInfo, userInfo])

  return (
    <SelectBox>
      <MyClass
        myclass={`${pre_class}/${pre_date}/${pre_time}/${pre_fewMinute}`}
        text='선택하신 교육 정보를 확인해 주세요.'
        fontSize='23px'
      />
      <Btn onClick={handleResultSubmitClick} text='해당 교육 신청 하기' />
    </SelectBox>
  )
}
