import styled from 'styled-components'
import { ExclamationIcon } from '../assets/svgs'

export default function MyClass({ myclass, text, fontSize }) {
  const myclassInfoArr = myclass.split('/')
  const year = myclassInfoArr[1].slice(0, 4)
  const month = myclassInfoArr[1].slice(4, 6)
  const date = myclassInfoArr[1].slice(6, 8)
  const dayArr = ['일', '월', '화', '수', '목', '금', '토']
  const day = myclassInfoArr[1].slice(8, 9)
  return (
    <Contents>
      <ClassInfo text={myclassInfoArr[0].length}>{myclassInfoArr[0]}</ClassInfo>
      <ClassInfo>{year}년</ClassInfo>
      <ClassInfo>
        {month}월 {date}일 {dayArr[day]}요일
      </ClassInfo>
      <ClassInfo>
        {myclassInfoArr[2].slice(0, 2)}시 {myclassInfoArr[2].slice(2, 4)}분
      </ClassInfo>
      <ClassInfo>교육시간 {myclassInfoArr[3]}분</ClassInfo>
      <Text fontSize={fontSize}>{text}</Text>
      <Message>
        <ExclamationIcon /> 취소는 교육 시작 2일 전까지만 가능합니다.
      </Message>
    </Contents>
  )
}

const Contents = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  font-weight: 500;
  margin: 20px 0 0 0;
  font-family: 'Noto Sans KR', sans-serif;
`

const ClassInfo = styled.div`
  padding: 10px 20px;
  text-align: center;
  margin: 5px;
  font-size: ${(props) => {
    if (props.text > 10) {
      return '20px'
    } else {
      return '25px'
    }
  }};
  font-weight: 500;
`

const Text = styled.div`
  padding: 10px;
  margin-top: 20px;
  font-size: ${(props) => props.fontSize};
  font-weight: 500;
  @media screen and (max-width: 400px) {
    font-size: 20px;
  }
`
const Message = styled.span`
  display: flex;
  color: red;
  margin-top: 10px;
  svg {
    width: 18px;
    height: 18px;
    fill: red;
  }
  @media screen and (max-width: 400px) {
    font-size: 15px;
    svg {
      width: 13px;
      height: 13px;
    }
  }
`
