export const dateStamp = () => {
  const today = new Date()
  const year = today.getFullYear()
  const month = `0${today.getMonth() + 1}`.slice(-2)
  const date = `0${today.getDate()}`.slice(-2)
  const hour = `0${today.getHours()}`.slice(-2)
  const minute = `0${today.getMinutes()}`.slice(-2)
  const seconds = `0${today.getSeconds()}`.slice(-2)

  return year + month + date + hour + minute + seconds
}

export const idStamp = () => {
  const today = new Date()
  const year = today.getFullYear()
  const month = `0${today.getMonth() + 1}`.slice(-2)
  const hour = today.getHours()
  const minute = today.getMinutes()
  const seconds = today.getSeconds()
  const mSeconds = today.getMilliseconds()
  return year + month + hour + minute + seconds + mSeconds
}

export function classIdStamp(a) {
  var newDate = new Date(a)
  var year = newDate.getFullYear()
  var month = ('0' + (1 + newDate.getMonth())).slice(-2)
  var date = ('0' + newDate.getDate()).slice(-2)
  const day = newDate.getDay()
  return year + month + date + day
}

export function getBarToday() {
  var today = new Date()
  var year = today.getFullYear()
  var month = ('0' + (1 + today.getMonth())).slice(-2)
  var date = ('0' + today.getDate()).slice(-2)

  return year + '-' + month + '-' + date
}

export function getBarPlus6Day(today) {
  var date = new Date(today)
  var year = date.getFullYear()
  var month = ('0' + (1 + date.getMonth())).slice(-2)
  var day = ('0' + (date.getDate() + 6)).slice(-2)

  return year + '-' + month + '-' + day
}
